import { useState } from "react"

const platforms = [
  "AWS",
  "Azure",
  "GCP",
  "Vercel",
  "Docker",
  "Railway",
  "Render"
]

export default function PlatformPage() {
  const [selected, setSelected] = useState("")

  const handleDeploy = () => {
    if (!selected) {
      alert("Please select a platform")
      return
    }

    localStorage.setItem("deploy_platform", selected)
    alert(`Platform selected: ${selected}`)

    // Later: redirect to deployment progress page
  }

  return (
    <div style={styles.container}>
      <h2>Select Deployment Platform</h2>

      <div style={styles.grid}>
        {platforms.map(p => (
          <div
            key={p}
            onClick={() => setSelected(p)}
            style={{
              ...styles.card,
              border: selected === p ? "2px solid black" : "1px solid gray"
            }}
          >
            {p}
          </div>
        ))}
      </div>

      <button style={styles.button} onClick={handleDeploy}>
        Continue
      </button>
    </div>
  )
}

const styles = {
  container: {
    padding: "40px",
    textAlign: "center"
  },
  grid: {
    display: "grid",
    gridTemplateColumns: "repeat(auto-fit, minmax(120px, 1fr))",
    gap: "16px",
    maxWidth: "600px",
    margin: "20px auto"
  },
  card: {
    padding: "20px",
    cursor: "pointer",
    borderRadius: "6px"
  },
  button: {
    marginTop: "20px",
    padding: "10px 20px",
    cursor: "pointer"
  }
}
